<template>
  <div id="customerDetails">
    <div class="container">
      <router-link to="/" class="btn btn-default">返回</router-link>
      <h1 class="page-header">
        姓名：{{customer.name}}
        <span class="pull-right">
          <router-link v-bind:to="'/edit/'+customer.id" class="btn btn-primary">编辑</router-link>
          <button v-on:click="deleteCustomer" class="btn btn-danger">删除</button>
        </span>
      </h1>
      <ul class="list-group">
        <li class="list-group-item">
          <span class="glyphicon glyphicon-asterisk">电话：{{customer.phone}}</span>
        </li>
        <li class="list-group-item">
          <span class="glyphicon glyphicon-plus">邮箱：{{customer.email}}</span>
        </li>
      </ul>
      <ul class="list-group">
        <li class="list-group-item">
          <span class="glyphicon glyphicon-asterisk">学历：{{customer.education}}</span>
        </li>
        <li class="list-group-item">
          <span class="glyphicon glyphicon-plus">毕业学校：{{customer.graduationSchool}}</span>
        </li>
        <li class="list-group-item">
          <span class="glyphicon glyphicon-asterisk">职业：{{customer.profession}}</span>
        </li>
        <li class="list-group-item">
          <span class="glyphicon glyphicon-plus">个人简介：{{customer.profile}}</span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "customerDetails",
  data() {
    return {
      id: this.$route.params.id,
      customer: ""
    };
  },
  created() {
    this.$axios.get("/customer/" + this.id).then(res => {
      this.customer = res.data;
    });
  },
  methods: {
    deleteCustomer() {
      this.$axios.delete("/customer/" + this.id, this.customer).then(res => {
        this.$router.push({ path: "/", query: { alert: "删除成功！" } });
      });
    }
  }
};
</script>

