<template>
  <div id="customer" class="container">
    <alert v-show="alert" v-bind:message="alert"></alert>
    <br>
    <h1 class="page-header">用户管理系统</h1>
    <input type="text" class="form-control" placeholder="请输入搜索内容" v-model="search">
    <table class="table table-striped">
      <thead>
        <tr>
          <th>姓名</th>
          <th>电话</th>
          <th>邮箱</th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(customer,index) in filterCustomer" :key="index">
          <td>{{customer.name}}</td>
          <td>{{customer.phone}}</td>
          <td>{{customer.email}}</td>
          <td>
            <router-link v-bind:to="'/customerDefailt/' +customer.id" class="btn btn-default">详情</router-link>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import Alert from "./Alert";
export default {
  name: "customer",
  data() {
    return {
      customers: [],
      search: "",
      alert: ""
    };
  },
  created() {
    if (this.$route.query.alert) {
      this.alert = this.$route.query.alert;
    }
    this.getCustomers();
  },
  updated() {
    this.getCustomers();
  },
  computed: {
    filterCustomer() {
      return this.customers.filter(customer => {
        return customer.name.match(this.search);
      });
    }
  },
  methods: {
    getCustomers() {
      this.$axios.get("/customer").then(res => {
        // console.log(res);
        this.customers = res.data;
      });
    }
  },
  components: {
    Alert
  }
};
</script>

