<template>
  <div id="edit" class="container">
    <alert v-show="alert" v-bind:message="alert"></alert>
    <h1 class="page-header">编辑用户信息</h1>
    <form v-on:submit.prevent="editCustomer">
      <div class="well">
        <h4>用户信息</h4>
        <div class="for-group">
          <label>姓名</label>
          <input type="text" class="form-control" placeholder="name" v-model="customer.name">
        </div>
        <div class="for-group">
          <label>电话</label>
          <input type="text" class="form-control" placeholder="phone" v-model="customer.phone">
        </div>
        <div class="for-group">
          <label>邮箱</label>
          <input type="text" class="form-control" placeholder="email" v-model="customer.email">
        </div>
        <div class="for-group">
          <label>学历</label>
          <input
            type="text"
            class="form-control"
            placeholder="education"
            v-model="customer.education"
          >
        </div>
        <div class="for-group">
          <label>毕业学校</label>
          <input
            type="text"
            class="form-control"
            placeholder="graduationSchool"
            v-model="customer.graduationSchool"
          >
        </div>
        <div class="for-group">
          <label>职业</label>
          <input
            type="text"
            class="form-control"
            placeholder="profession"
            v-model="customer.profession"
          >
        </div>
        <div class="for-group">
          <label>个人简介</label>
          <textarea
            class="form-control"
            cols="30"
            rows="10"
            style="resize:none;"
            v-model="customer.profile"
          ></textarea>
        </div>
        <button type="submit" class="btn btn-primary">确认编辑</button>
      </div>
    </form>
  </div>
</template>

<script>
import Alert from "./Alert";
export default {
  name: "edit",
  data() {
    return {
      id: this.$route.params.id,
      customer: {},
      alert: ""
    };
  },
  created() {
    this.$axios.get("/customer/" + this.id).then(res => {
      this.customer = res.data;
    });
  },
  methods: {
    editCustomer() {
      if (!this.customer.name) {
        this.alert = "用户名不能为空";
        return;
      } else if (!this.customer.phone) {
        this.alert = "电话号码不能为空";
        return;
      } else if (!this.customer.email) {
        this.alert = "邮箱不能为空";
        return;
      } else {
        this.$axios.put("/customer/" + this.id, this.customer).then(res => {
          this.$router.push({
            path: "/",
            query: { alert: "编辑用户信息成功！" }
          });
        });
      }
    }
  },
  components: {
    Alert
  }
};
</script>

<style scoped>
</style>
